<script>
import {mapActions} from "vuex";

export default {
    name:"UserJoin",
    computed: {
        idState() { 
            return this.user.userId.length > 4 && this.user.userId.length < 12;
        },
        nameState() { 
            return this.user.name.length >=3 && this.user.name.length < 12;
        },
        passwordState() { 
            return this.user.password.length >= 4 && this.user.password.length < 12;
        },
        passwordConfirmState() { 
            return this.user.passwordConfirm == this.user.password;
        },
        addressState() { 
            return this.user.address.length > 0&& this.user.address.length < 100;
        },
        IdinvalidFeedback() { 
            if (this.user.userId.length == 0) {
                return ' .';
            }
            if (this.user.userId.length < 4 || this.user.userId.length > 12) {
                return ' 4  12  .';
            }
            return " .";
        },
        nameInvalidFeedback() { 
            if (this.user.name.length == 0) {
                return ' .';
            }
            return " .";
        },
        passwordInvalidFeedback() { 
            if (this.user.password.length == 0) {
                return ' .';
            }
            if (this.user.password.length < 4 || this.user.password.length > 12) {
                return ' 4  12  .';
            }
            return " .";
        },
        passwordConfirmInvalidFeedback() { 
            if (this.user.passwordConfirm.length == 0) {
                return ' .';
            }
            if (this.user.passwordConfirm != this.user.password) {
                return '  .';
            }
            return " .";
        },
        addressInvalidFeedback() { 
            if (this.user.address.length == 0) {
                return ' .';
            }

            return " .";
        },
        emailState() { 
            const emailRegex = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/;
            return emailRegex.test(this.user.email);
        },
        emailInvalidFeedback() { 
            if (this.user.email.length == 0) {
                return ' .';
            }
            const emailRegex = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/;
            if (!emailRegex.test(this.user.address)) {
                return '  .';
            }
            return " .";
        }
    },
    data() {
        return {
            user:{
                userId:"",
                name:"",
                password: "",
                passwordConfirm:"",
                email:"",
                address:"",
            },
        }
    },
    methods:{
        ...mapActions(`userStore`,[`userJoin`]),
        joinConfirm() {
            //  valid feed back  ,  
            if (this.idState && this.nameState && this.passwordState && this.passwordConfirmState && this.addressState && this.emailState) {
                this.userJoin(this.user);
            }
            else {
                alert("   .");
            }
        }
    }
}
</script>

<template>
    <b-container class="mt-5" id="joinContainer">
        <b-row>
            <b-col></b-col>
            <b-col cols="6">
                <p class="text-center font-weight-bold" id="user-join-title"> </p>
            </b-col>
            <b-col></b-col>
        </b-row>
        <b-row class="mt-5">
            <b-col></b-col>
            <b-col cols="6">
                <b-form>
                    <b-form-group :invalid-feedback="IdinvalidFeedback" valid-feedback=" ." :state="idState">
                        <b-form-input type="text" v-model="user.userId" placeholder="" trim></b-form-input>
                    </b-form-group>

                    <b-form-group :invalid-feedback="nameInvalidFeedback" valid-feedback=" ." :state="nameState">
                        <b-form-input type="text" v-model="user.name" placeholder="" trim></b-form-input>
                    </b-form-group>

                    <b-form-group :invalid-feedback="passwordInvalidFeedback" valid-feedback=" ." :state="passwordState">
                        <b-form-input type="password" v-model="user.password" placeholder="" trim></b-form-input>
                    </b-form-group>

                    <b-form-group :invalid-feedback="passwordConfirmInvalidFeedback" valid-feedback=" ." :state="passwordConfirmState" >
                        <b-form-input type="password" v-model="user.passwordConfirm" placeholder=" " trim ></b-form-input>
                    </b-form-group>

                    <b-form-group :invalid-feedback="addressInvalidFeedback" valid-feedback=" " :state="addressState">
                        <b-form-input type="text" v-model="user.address" placeholder="" trim></b-form-input>
                    </b-form-group>

                    <b-form-group :invalid-feedback="emailInvalidFeedback" valid-feedback=" " :state="emailState">
                        <b-form-input type="email"  v-model="user.email" placeholder=""  trim></b-form-input>
                    </b-form-group>
                    <b-button @click="joinConfirm" block></b-button>
                </b-form>
            </b-col>
            <b-col></b-col>
        </b-row>
    </b-container>
</template>

